___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --background: #FBFDEB;
  --foreground: #1C1C18;
  --primary: #74AC4D;
  --primary-foreground: #FBFDEB;
  --secondary: #EBEECB;
  --destructive: #B11441;
  --border: #e5e7eb;
  --ring: #0e7490;
  --radius: 0.5rem;
}

@media (prefers-color-scheme: dark) {
  :root {
    --background: #000000;
    --foreground: #fafafa;
    --primary: #22d3ee;
    --secondary: #222222;
    --destructive: #B11441;
    --border: #27272a;
    --ring: #22d3ee;
  }
}

@font-face {
  font-family: "San Francisco";
  font-weight: 400;
  src: local(".AppleSystemUIFontRegular");
}

@font-face {
  font-family: "San Francisco";
  font-weight: 500;
  src: local(".AppleSystemUIFontMedium");
}

@font-face {
  font-family: "San Francisco";
  font-weight: 600;
  src: local(".AppleSystemUIFontSemibold");
}

body {
  font-family: -apple-system, BlinkMacSystemFont, avenir next, avenir, segoe ui, helvetica neue, helvetica, Cantarell, Ubuntu, roboto, noto, arial, sans-serif;
  background-color: var(--background);
  color: var(--foreground);
  line-height: 1.2;
  /* -webkit-font-smoothing: antialiased; */
  height: 100vh;
  padding: 0;
}
